.newcollection {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin-bottom: 100px;
}

.newcollection h1 {
    color: #171717;
    font-size: 50px;
    font-weight: 600;
}

.newcollection hr {
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}

.collections {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin: 50px 0 auto;
    gap: 20px;
}