.loginSignUp {
    width: 100%;
    height: 90vh;
    background: #fce3fe;
    padding-top: 60px;
}

.loginSignUp-container {
    width: 500px;
    height: 500px;
    background: white;
    margin: auto;
    padding: 40px 60px;
}

.loginSignUp-container h1 {
    margin: 20px 0px;
}

.loginSignUp-fields {
    display: flex;
    flex-direction: column;
    gap: 29px;
    margin-top: 30px;
}

.loginSignUp-fields input {
    height: 50px;
    padding-left: 20px;
    border: 1px solid #c9c9c9;
    outline: none;
    color: #5c5c5c;
    font-size: 18px;
}

.loginSignUp-container button {
    width: 100%;
    margin-top: 30px;
    height: 50px;
    color: white;
    background: #ff4141;
    border: none;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
}

.loginSignUp-login {
    margin-top: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
}

.loginSignUp-login span {
    color: #ff4141;
    font-weight: 600;
}

.loginSignUp-agree {
    display: flex;
    align-items: center;
    margin-top: 25px;
    gap: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
}

