.relatedProducts {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    gap: 10px;
}

.relatedProducts h1 {
    color: #171717;
    font-size: 60px;
    font-weight: 600;
}

.relatedProducts hr {
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}

.relatedProducts-item {
    margin-top: 40px;
    display: flex;
    gap: 10px;
}