.shopCategory-banner {
    display: block;
    margin: 30px auto;
    width: 82%;
}

.shopCategory-indexSort {
    display: flex;
    margin: 0 170px;
    justify-content: space-between;
    align-items: center;
}

.shopCategory-sort {
    padding: 10px 20px;
    border-radius: 40px;
    border: 1px solid #888;
}

.shopCategory-indexSort p span {
    font-weight: 600;
}

.shopCategory-products {
    margin: 20px auto;
    padding: 0 130px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 80px 30px;
}

.shopCategory-loadMore {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 150px auto;
    width: 233px;
    height: 69px;
    border-radius: 75px;
    background: #ededed;
    color: #787878;
    font-size: 18px;
    font-weight: 500;
}